import React, { ReactNode } from 'react';
//import{ Link as Scroll } from 'react-scroll'
import {HashLink} from 'react-router-hash-link';
import './ContentCommon.css';

interface LinkProps {
  // Props
  to: string;
  children: ReactNode; // children 
}

/* Programming */
const LinkP: React.FC<LinkProps> = (props) => {
  
  const scrollWithOffset = (el: HTMLElement | null) => {
    if (el) {
      window.scrollTo({
        top: el.getBoundingClientRect().top + window.scrollY - 80,
        behavior: 'smooth',
      });
    }
  };

  return <HashLink className="scroll-link" to={`${props.to}`} smooth={true} scroll={el => scrollWithOffset(el)}>{props.children}</HashLink>;
};

interface Component2Props {
  // Props
}

const Component2: React.FC<Component2Props> = (props) => {
  // 
  return <div>Component 2</div>;
};

// 
const Component3: React.FC = () => {
  return <div>Component 3</div>;
};

export { LinkP, Component2, Component3 };
