// SideBar.tsx

import React, { FC, useState, useEffect } from 'react';
import './Navigator.css';

interface SideBarProps {
  onLinkClicked: (items: SidebarItem, practiceId: number) => void;
  isOpen: boolean;
  selectLesson: SidebarItem;
  dispList: SidebarItem[];
  practiceId: number;
}

interface SidebarItem {
  label: string;
  link: string;
  nestedList?: SidebarItem[];
}

const SideBar: FC<SideBarProps> = ({ onLinkClicked, isOpen, selectLesson, dispList, practiceId }) => {
  
  const handleItemClick = (item: SidebarItem, practiceId: number) => {
    onLinkClicked(item,practiceId);
    console.log(`Link clicked in Sidebar: ${item.label}`);
  };

  return (
    <div className={`sidebar ${isOpen ? 'open' : ''}`}>
      <div className="sidebar-list">
        <ul>
          {dispList.map((item) => (
            <li>
              <button onClick={() => handleItemClick(item,practiceId)}>{item.label}</button>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default SideBar;